import Vue from 'vue';
import VueRouter from 'vue-router';
import Index from '@/page/index.vue';
import List from '@/page/list.vue';
import Create from '@/page/create.vue';
import Read from '@/page/read.vue';
import Update from '@/page/update.vue';
import Delete from '@/page/delete.vue';
import DeleteR from '@/page/deleteR.vue';
import UpdateR from '@/page/updateR.vue';
import CreateR from '@/page/createR.vue';

Vue.use(VueRouter);
export const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      redirect:'/qna/list',
    },
    {
      path: '/qna',
      name: 'index',
      component: Index,
    },
    {
      path: '/qna/list',
      name: 'list',
      component: List,
    },
    {
      path: '/qna/create',
      name: 'create',
      component: Create,
    },
    {
      path: '/qna/read',
      name: 'read',
      component: Read,
    },
    {
      path: '/qna/update',
      name: 'update',
      component: Update,
    },
    {
      path: '/qna/delete',
      name: 'delete',
      component: Delete,
    },
    {
      path: '/qna/createR',
      name: 'createR',
      component: CreateR,
    },
    {
      path: '/qna/updateR',
      name: 'updateR',
      component: UpdateR,
    },
    {
      path: '/qna/deleteR',
      name: 'deleteR',
      component: DeleteR,
    },
  ],
});
