<template>
  <div>
    <div class="form-group">
      <label for="qnaUserid">작성자</label>
      <input
        type="text"
        class="form-control"
        id="qnaUserid"
        ref="qnaUserid"
        placeholder="작성자를 입력하세요"
        v-model="qnaUserid"
        readonly
      />
    </div>
    <div class="form-group">
      <label for="qnaTitle">제목</label>
      <input
        type="text"
        class="form-control"
        id="qnaTitle"
        ref="qnaTitle"
        placeholder="제목을 입력하세요"
        v-model="qnaTitle"
        readonly
      />
    </div>
    <div class="form-group">
      <label for="qnaContent">내용</label>
      <textarea
        type="text"
        class="form-control"
        id="qnaContent"
        ref="qnaContent"
        placeholder="내용을 입력하세요"
        v-model="qnaContent"
        readonly
      ></textarea>
    <br>
    </div>
    <div class="form-group">
      <label for="replyContent">답글 내용</label>
      <textarea
        type="text"
        class="form-control"
        id="replyContent"
        ref="replyContent"
        placeholder="내용을 입력하세요"
        v-model="replyContent"
      ></textarea>
    </div>
    <br>
    <div class="text-right">
      <button
        class="btn btn-primary"
        v-if="type == 'createR'"
        @click="checkHandler"
      >
        등록
      </button>
      <button class="btn btn-primary" v-else @click="checkHandler">수정</button>
      <button class="btn btn-primary" @click="moveList">목록</button>
    </div>
  </div>
</template>

<script>
import http from '@/util/http-common';
import store from "@/store/store.js";

export default {
  name: 'boardR-Form',
  props: {
    type: { type: String }
  },
  data: function() {
    return {
      qnaNo: '',
      qnaUserid: '',
      qnaTitle: '',
      qnaContent: '',
      replyContent: '',
      uid: '',
    };
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = '';
      err &&
        !this.replyContent &&
        ((msg = '내용 입력해주세요'),
        (err = false),
        this.$refs.replyContent.focus());

      if (!err) alert(msg);
      else this.type == 'createR' ? this.createHandler() : this.updateHandler();
    },
    createHandler() {
      http
        .put(`/qna/reply/${this.qnaNo}`, {
          qnaNo: this.qnaNo,
          qnaUserid: this.qnaUserid,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
          replyContent: this.replyContent,
          replyUserid: this.uid,
        })
        .then(({ data }) => {
          let msg = '답글 등록 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '답글 등록이 완료되었습니다.';
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert('답글 등록 처리시 에러가 발생했습니다.');
        });
    },
    updateHandler() {
      http
        .put(`/qna/reply/${this.qnaNo}`, {
          qnaNo: this.qnaNo,
          qnaUserid: this.qnaUserid,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
          replyContent: this.replyContent,
          replyUserid: this.uid,
        })
        .then(({ data }) => {
          let msg = '답글 수정 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '답글 수정이 완료되었습니다.';
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert('답글 수정 처리시 에러가 발생했습니다.');
        });
    },
    moveList() {
      this.$router.push('/qna/list');
    },
  },
  created() {
    this.uid = store.state.uid;
    
    http
      .get(`/qna/${this.$route.query.qnaNo}`)
      .then(({ data }) => {
        this.qnaNo = data.qnaNo;
        this.qnaUserid = data.qnaUserid;
        this.qnaTitle = data.qnaTitle;
        this.qnaContent = data.qnaContent;
        this.replyUserid = data.replyUserid;
        this.replyContent = data.replyContent;
      })
      .catch(() => {
        alert('에러가 발생했습니다.');
      });
  },
};
</script>

<style scoped></style>
