<template>
  <div align="center">
    <table class="table table-bordered w-50">
      <tr>Q.</tr>
      <tr>
        <th>번호</th>
        <td>{{ qnaNo }}</td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td>{{ qnaUserid }}</td>
      </tr>
      <tr>
        <th>제목</th>
        <td>{{ qnaTitle }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(qnaDatetime) }}</td>
      </tr>
      <tr>
        <td colspan="2">
          {{ qnaContent }}
        </td>
      </tr>
    </table>
    
    <br />

    <table class="table table-bordered w-50" v-if="replyContent!=null">
      <tr>A.</tr>
      <tr>
        <th>답변 날짜</th>
        <td>{{ getFormatDate(replyDatetime) }}</td>
      </tr>
      <tr>
        <th>답변 내용</th>
        <td colspan="2">
          {{ replyContent }}
        </td>
      </tr>
    </table>

    <br />
    <div class="text-center">
      <router-link to="/qna/list"
        ><button class="btn btn-primary">목록</button></router-link
      >
      <router-link :to="'/qna/update?qnaNo=' + qnaNo"
        ><button class="btn btn-primary">수정</button></router-link
      >
      <router-link :to="'/qna/delete?qnaNo=' + qnaNo"
        ><button class="btn btn-primary">삭제</button></router-link
      >
      <router-link :to="'/qna/createR?qnaNo=' + qnaNo"
        ><button v-if="uid=='admin' && replyContent==null" class="btn btn-primary">답글 등록</button></router-link
      >
      <router-link :to="'/qna/updateR?qnaNo=' + qnaNo"
        ><button v-if="uid=='admin' && replyContent!=null" class="btn btn-primary">답글 수정</button></router-link
      >
      <router-link :to="'/qna/deleteR?qnaNo=' + qnaNo"
        ><button v-if="uid=='admin' && replyContent!=null" class="btn btn-primary">답글 삭제</button></router-link
      >
    </div>
  </div>
</template>

<script>
import moment from 'moment';
export default {
  name: 'detail',
  props: {
    qnaNo: { type: Number },
    qnaUserid: { type: String },
    qnaTitle: { type: String },
    qnaContent: { type: String },
    qnaDatetime: { type: String },
    replyContent: { type: String },
    replyDatetime: { type: String },
    replyUserid: { type: String },
    uid: { type: String },
  },
  methods: {
    getFormatDate(qnaDatetime) {
      // console.log(this.uid);
      return moment(new Date(qnaDatetime)).format('YYYY.MM.DD HH:mm:ss');
    },
  },
};
</script>
