<template>
  <div>
    <detail
      :qnaContent="item.qnaContent"
      :qnaTitle="item.qnaTitle"
      :qnaUserid="item.qnaUserid"
      :qnaDatetime="item.qnaDatetime"
      :qnaNo="item.qnaNo"
      :replyContent="item.replyContent"
      :replyDatetime="item.replyDatetime"
      :replyUserid="item.replyUserid"
      :uid="uid"
    />
  </div>
</template>

<script>
import http from '@/util/http-common';
import Detail from '@/components/Detail.vue';
import store from "@/store/store.js";

export default {
  name: 'read',
  components: {
    Detail,
  },
  data: function() {
    return {
      item: {},
      uid: '',
    };
  },
  created() {
    this.uid = store.state.uid;

    http.get(`/qna/${this.$route.query.qnaNo}`).then(({ data }) => {
      this.item = data;
      console.dir(data);
    });
  },
};
</script>

