<template>
  <div>
    <div v-if="items.length">
      <table class="table table-bordered table-condensed">
        <colgroup>
          <col :style="{ width: '5%' }" />
          <col :style="{ width: '50%' }" />
          <col :style="{ width: '10%' }" />
          <col :style="{ width: '25%' }" />
        </colgroup>
        <tr>
          <th>번호</th>
          <th>제목</th>
          <th>작성자</th>
          <th>날짜</th>
        </tr>
        <list-row
          v-for="(item, index) in items"
          :key="`${index}_items`"
          :qnaNo="item.qnaNo"
          :qnaTitle="item.qnaTitle"
          :qnaUserid="item.qnaUserid"
          :qnaDatetime="item.qnaDatetime"
          :replyContent="item.replyContent"
          :replyDatetime="item.replyDatetime"
          :replyUserid="item.replyUserid"
          :uid="uid"
        />
      </table>
    </div>
    <div v-else>글이 없습니다.</div>
    <div class="text-right" v-if="isLogin">
      <button class="btn btn-primary" @click="movePage">등록</button>
    </div>
  </div>
</template>

<script>
import http from '@/util/http-common';
import ListRow from '@/components/Row.vue';
import store from "@/store/store.js";

export default {
  name: 'list',
  components: {
    ListRow,
  },
  data: function() {
    return {
      items: [],
      isLogin: false,
      uid: '',
    };
  },
  created() {
    this.uid = store.state.uid;
    
    http
      .get('/qna')
      .then(({ data }) => {
        console.log(data);
        this.items = data;
      })
      .catch(() => {
        alert('에러가 발생했습니다.');
      });
  },
  methods: {
    movePage() {
      this.$router.push('/qna/create');
    },
  },
};
</script>

