<template>
  <div>삭제중...</div>
</template>

<script>
import http from '@/util/http-common';
export default {
  name: 'deleteR',
  created() {
    http
      .put(`/qna/reply/delete/${this.$route.query.qnaNo}`, {
          qnaNo: this.$route.query.qnaNo,
          qnaUserid: this.qnaUserid,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
          replyContent: this.replyContent,
          replyUserid: this.uid,
        })
      .then(({ data }) => {
        let msg = '답글 삭제 처리시 문제가 발생했습니다.';
        if (data === 'success') {
          msg = '답글 삭제가 완료되었습니다.';
        }
        console.log(data);
        alert(msg);
        this.$router.push('/qna/list');
      })
      .catch(() => {
        alert('답글 삭제 처리시 에러가 발생했습니다.');
      });
  },
};
</script>

