<template>
  <tr>
    <td>{{ qnaNo }}</td>
    <td v-if="qnaUserid==uid || uid=='admin'">
      <router-link :to="`/qna/read?qnaNo=${qnaNo}`">{{ qnaTitle }}</router-link>
    </td>
    <td v-else>{{ qnaTitle }}</td>
    <td>{{ qnaUserid }}</td>
    <td>{{ getFormatDate(qnaDatetime) }}</td>
  </tr>
</template>

<script>
import moment from 'moment';
export default {
  name: 'row',
  props: {
    qnaNo: { type: Number },
    qnaUserid: { type: String },
    qnaTitle: { type: String },
    qnaDatetime: { type: String },
    uid: { type : String },
  },
  methods: {
    getFormatDate(qnaDatetime) {
      return moment(new Date(qnaDatetime)).format('YYYY.MM.DD');
    },
  },
};
</script>
